/* ----- Global Styles ----- */

:root {
  --primary: #7f878e;
  --primary-rgb: 127, 135, 142;
  --secondary: #FFF7F1;
  --secondary-rgb: 255, 247, 241;
  --tertiary: #32231A;
  --tertiary-rgb: 50, 35, 26;
  --quaternary: #fc6c24;
  --quaternary-rgb: 252, 108, 36;

  --font-large: 20px;
  --font-medium-large: 20px;
  --font-medium: 14px;
  --font-normal: 12px;
  --font-small: 10px;

  --line-thick: 4px;
  --line-medium: 3px;
  --line-thin: 2px;
}

