body, html{
  height: 100vh;
  display: grid;
  justify-content: center;
  align-content: center;
  font-family: 'Space Mono', monospace;
  color: var(--primary);
}

.vending-machine {
  position: relative;
  margin: auto;
  width: 700px;
  height: 690px;
  background-color: var(--secondary);
  border-radius: 30px;
  border: 10px solid var(--quaternary);
}

h2 {
  font-size: var(--font-large);
  margin: 15px;
  font-style: italic;
}

h3 {
  text-align: center;
  font-style: italic;
  margin: 20px 0 0 0;
}


/* ----- Branding ----- */

.branding {
  font-weight: 800;
  font-size: var(--font-large);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.branding > img {
  margin-top: 12px;
  max-width: 300px;
}


/* ----- Money Input & Total ----- */

.money-container {
  display: flex;
  flex-direction: row;
}

.money-input, .total {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.money-input {
  width: 65%;
}

.total {
  width: 35%;
}

.option {
  margin: 10px;
  padding: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  background-color: var(--secondary);
  color: var(--tertiary);
  text-decoration: none;
  border-radius: 50%;
  border: var(--line-medium) solid var(--quaternary);
  transition: all 0.25s;
}

.option:hover {
  background-color: var(--tertiary);
  color: var(--secondary);
}

.total-display {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 65px;
  border-radius: 20px;
  background-color: var(--tertiary);
  color: var(--quaternary);
  font-size: var(--font-large);
}


/* ----- Chocolates and Output ----- */

.chocolates-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.chocolate {
  display: flex;
  padding: 5px;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  cursor: pointer;
  border: var(--line-medium) solid var(--secondary);
  border-radius: 30px;
  transition: all 0.25s;
}

.chocolate:hover {
  border: var(--line-medium) solid var(--quaternary);
}

.chocolate > img {
  width: 150px;
}

.title {
  margin: 5px;
  color: var(--tertiary);
}

.price {
  margin-top: 3px;
}

.retrieve-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.retrieve-btn {
  margin: 10px;
  padding: 15px 20px;
  background-color: var(--secondary);
  color: var(--tertiary);
  text-decoration: none;
  border-radius: 20px;
  border: var(--line-medium) solid var(--quaternary);
  transition: all 0.25s;
}

.retrieve-btn:hover {
  background-color: var(--tertiary);
  color: var(--secondary);
}

.success-message {
  position: absolute;
  background-color: rgba(var(--secondary-rgb), 0.95);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.success-message > img {
  width: 350px;
  margin: 0;
}

.success-message > h2 {
  margin: 10px 0 30px 0;
}

.success-message > p {
  margin: 30px;
}

.more-chocolate-btn {
  margin: 10px;
  padding: 15px 20px;
  background-color: var(--secondary);
  color: var(--tertiary);
  text-decoration: none;
  border-radius: 20px;
  border: var(--line-medium) solid var(--quaternary);
  transition: all 0.25s;
}

.more-chocolate-btn:hover {
  background-color: var(--tertiary);
  color: var(--secondary);
}

.error-message {
  position: absolute;
  text-align: right;
  font-size: var(--font-medium);
  font-style: italic;
  color: var(--quaternary);
  right: 30px;
  bottom: 60px;
}

/* ----- Helpers ----- */

.unavailable {
  opacity: 0.2;
}

.selected {
  border-radius: 30px;
  border: var(--line-medium) solid var(--quaternary);
}

.hide {
  display: none;
}
